import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Routes, Route, Link } from 'react-router-dom';
import './App.css';

// Import Components
import HiraganaGame from './components/HiraganaGame';
// import KatakanaGame from './components/KatakanaGame'; // ()
 import VocabGame from './components/VocabGame';       // ()
// import HiraganaChart from './components/HiraganaChart'; // ()
// import KatakanaChart from './components/KatakanaChart'; // ()

import Login from './components/Login';

//  ()
function Home() {
  return (
    <div className="dashboard-container">
      <header>
        <h1>Japanese Master </h1>
        <p>!</p>
      </header>
      
      <div className="menu-section">
        <h3>  (Games)</h3>
        <div className="button-list">
          <Link to="/hiragana-game" className="menu-item"> (Hiragana)</Link>
          <Link to="/katakana-game" className="menu-item"> (Katakana)</Link>
          <Link to="/vocabulary-game" className="menu-item"> (Vocabulary)</Link>
        </div>
      </div>

      <div className="menu-section">
        <h3>  (Charts)</h3>
        <div className="button-list">
          <Link to="/chart-hiragana" className="menu-item secondary"></Link>
          <Link to="/chart-katakana" className="menu-item secondary"></Link>
        </div>
      </div>
    </div>
  );
}

function App() {
  const [username, setUsername] = useState(null);

  // 1.  Login 
  useEffect(() => {
    const savedName = localStorage.getItem('username');
    if (savedName) setUsername(savedName);
  }, []);

  // 2.  Login
  const handleLogin = (name) => {
    localStorage.setItem('username', name);
    setUsername(name);
  };

  // 3.  Logout (: )
  const handleLogout = () => {
    localStorage.removeItem('username');
    setUsername(null);
  };

  //  Login  Login
  if (!username) {
    return <Login onLogin={handleLogin} />;
  }

  //  Login   Routing
  return (
    <Router>
      <div className="app-container">
        
        {/* Navbar    Login   */}
        <nav style={{ 
          display: 'flex', 
          justifyContent: 'space-between', 
          padding: '10px 20px', 
          background: '#f0f2f5',
          marginBottom: '20px'
        }}>
          <div>
            <Link to="/" style={{ textDecoration: 'none', fontWeight: 'bold', color: '#333' }}> </Link>
          </div>
          <div>
             {username} | <button onClick={handleLogout} style={{cursor: 'pointer', border:'none', background:'none', color:'red'}}></button>
          </div>
        </nav>

        {/*  Route */}
        <Routes>
          <Route path="/" element={<Home />} />
          
          {/*  username   Firebase */}
          <Route path="/hiragana-game" element={<HiraganaGame username={username} />} />
          <Route path="/vocabulary-game" element={<VocabGame username={username} />} />
          
          {/* ( Comment  Error ) */}
          {/* <Route path="/katakana-game" element={<KatakanaGame username={username} />} /> */}
          {/* <Route path="/vocabulary-game" element={<VocabGame username={username} />} /> */}
          {/* <Route path="/chart-hiragana" element={<HiraganaChart />} /> */}
          {/* <Route path="/chart-katakana" element={<KatakanaChart />} /> */}
        </Routes>

      </div>
    </Router>
  );
}

export default App;